















import React from 'react';
import {
  Content,
  ContentHeader,
  ItemCardGrid,
  Link,
} from '@backstage/core-components';
import { Typography } from '@material-ui/core';
import { TemplateCard, } from './TemplateCard';
import { stringifyEntityRef } from '@backstage/catalog-model';









export const TemplateGroup = (props) => {
  const { templates, title, components: { CardComponent } = {} } = props;
  const titleComponent =
    typeof title === 'string' ? React.createElement(ContentHeader, { title: title,} ) : title;

  if (templates.length === 0) {
    return (
      React.createElement(Content, null
        , titleComponent
        , React.createElement(Typography, { variant: "body2",}, "No templates found that match your filter. Learn more about"
                   , ' '
          , React.createElement(Link, { to: "https://backstage.io/docs/features/software-templates/adding-templates",}, "adding templates"

          ), "."

        )
      )
    );
  }

  const Card = CardComponent || TemplateCard;

  return (
    React.createElement(Content, null
      , titleComponent
      , React.createElement(ItemCardGrid, null
        , templates.map(template => (
          React.createElement(Card, { key: stringifyEntityRef(template), template: template,} )
        ))
      )
    )
  );
};
