import React, { } from 'react';
import { Routes, Route, useOutlet } from 'react-router';
import { TemplateListPage } from '../TemplateListPage';
import { SecretsContextProvider } from '../TemplateWizardPage/SecretsContext';
import { TemplateWizardPage } from '../TemplateWizardPage';
import {

  FIELD_EXTENSION_WRAPPER_KEY,
  FIELD_EXTENSION_KEY,
  DEFAULT_SCAFFOLDER_FIELD_EXTENSIONS,
} from '../../extensions';

import { useElementFilter } from '@backstage/core-plugin-api';


import { selectedTemplateRouteRef } from '../../routes';

/**
 * The Props for the Scaffolder Router
 *
 * @alpha
 */










/**
 * The Scaffolder Router
 *
 * @alpha
 */
export const Router = (props) => {
  const { components: { TemplateCardComponent } = {} } = props;

  const outlet = useOutlet() || props.children;

  const customFieldExtensions = useElementFilter(outlet, elements =>
    elements
      .selectByComponentData({
        key: FIELD_EXTENSION_WRAPPER_KEY,
      })
      .findComponentData({
        key: FIELD_EXTENSION_KEY,
      }),
  );

  const fieldExtensions = [
    ...customFieldExtensions,
    ...DEFAULT_SCAFFOLDER_FIELD_EXTENSIONS.filter(
      ({ name }) =>
        !customFieldExtensions.some(
          customFieldExtension => customFieldExtension.name === name,
        ),
    ),
  ];

  return (
    React.createElement(Routes, null
      , React.createElement(Route, {
        element: 
          React.createElement(TemplateListPage, {
            TemplateCardComponent: TemplateCardComponent,
            groups: props.groups,}
          )
        ,}
      )

      , React.createElement(Route, {
        path: selectedTemplateRouteRef.path,
        element: 
          React.createElement(SecretsContextProvider, null
            , React.createElement(TemplateWizardPage, { customFieldExtensions: fieldExtensions,} )
          )
        ,}
      )
    )
  );
};
