
















import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import React from 'react';
import { Link as RouterLink, } from 'react-router-dom';
import AddCircleOutline from '@material-ui/icons/AddCircleOutline';
import { catalogEntityCreatePermission } from '@backstage/plugin-catalog-common';
import { usePermission } from '@backstage/plugin-permission-react';

/**
 * Properties for {@link RegisterExistingButton}
 *
 * @alpha
 */




/**
 * A button that helps users to register an existing component.
 * @alpha
 */
export const RegisterExistingButton = (props) => {
  const { title, to } = props;
  const { allowed } = usePermission({
    permission: catalogEntityCreatePermission,
  });
  const isXSScreen = useMediaQuery(theme =>
    theme.breakpoints.down('xs'),
  );

  if (!to || !allowed) {
    return null;
  }

  return isXSScreen ? (
    React.createElement(IconButton, {
      component: RouterLink,
      color: "primary",
      title: title,
      size: "small",
      to: to,}
    
      , React.createElement(AddCircleOutline, null )
    )
  ) : (
    React.createElement(Button, { component: RouterLink, variant: "contained", color: "primary", to: to,}
      , title
    )
  );
};
